.main_container{
    line-height: 0;
   
    -webkit-column-count: 5;
    -webkit-column-gap:   0px;
    -moz-column-count:    5;
    -moz-column-gap:      0px;
    column-count:         5;
    column-gap:           0px;  

    animation-name: preload-img-container;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-timing-function: linear; 
   
}

.image{
    width: 100% !important;
    height: auto !important;
}

@media (max-width: 1200px) {
    .main_container {
    -moz-column-count:    4;
    -webkit-column-count: 4;
    column-count:         4;
    }
  }
  @media (max-width: 1000px) {
    .main_container {
    -moz-column-count:    3;
    -webkit-column-count: 3;
    column-count:         3;
    }
  }
  @media (max-width: 800px) {
    .main_container {
    -moz-column-count:    2;
    -webkit-column-count: 2;
    column-count:         2;
    }
  }
  @media (max-width: 400px) {
    .main_container {
    -moz-column-count:    1;
    -webkit-column-count: 1;
    column-count:         1;
    }
  }

  @keyframes preload-img-container {

    0% {
      margin-left: 0;
    }
  
    100% {
      margin-left: 58px;
    }
  
  }